import { Accordion, AccordionContent, AccordionItem, AccordionTrigger } from "@/components/ui/accordion"

const faqs = [
  {
    question: "Comment fonctionne l'intgration Vapi?",
    answer:
      "Notre systme s'intgre directement avec Vapi pour grer vos appels. Vous recevez un numro ddi ou redirigez vos appels vers notre plateforme. L'IA prend en charge les conversations et cre automatiquement les rendez-vous dans votre dashboard.",
  },
  {
    question: "Puis-je changer de secteur aprs inscription?",
    answer:
      "Oui, vous pouvez changer de secteur  tout moment depuis votre dashboard. L'interface s'adaptera automatiquement aux spcificits de votre nouveau secteur d'activit.",
  },
  {
    question: "Mes donnes sont-elles scurises?",
    answer:
      "Absolument. Toutes vos donnes sont chiffres en transit et au repos. Nous sommes conformes au RGPD et nos serveurs sont hbergs en Europe. Vous gardez le contrle total sur vos donnes.",
  },
  {
    question: "Puis-je essayer gratuitement?",
    answer:
      "Oui ! Notre plan Starter est gratuit et vous permet de dcouvrir la plateforme. Vous avez accs  la lecture de vos rendez-vous et au journal d'appels basique. Passez  Pro ou Elite quand vous tes prt.",
  },
  {
    question: "Comment les Smart Triggers fonctionnent?",
    answer:
      "Les Smart Triggers (disponibles en Elite) permettent d'automatiser des actions : rappels automatiques, confirmations, relances clients, SMS marketing... Tout se configure depuis votre dashboard.",
  },
  {
    question: "Le support est-il disponible?",
    answer:
      "Oui, nous offrons un support email pour tous les plans. Les plans Pro bnficient d'un support prioritaire, et les plans Elite ont accs  un Account Manager ddi disponible par tlphone.",
  },
]

export function FAQ() {
  return (
    <section id="faq" className="py-24 bg-secondary/20">
      <div className="max-w-3xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="text-center mb-16">
          <h2 className="text-3xl sm:text-4xl font-bold text-foreground">Questions Frquentes</h2>
          <p className="mt-4 text-lg text-muted-foreground">Tout ce que vous devez savoir sur ReceptionAI</p>
        </div>

        <Accordion type="single" collapsible className="space-y-4">
          {faqs.map((faq, index) => (
            <AccordionItem key={index} value={`item-${index}`} className="glass-card rounded-xl px-6 border-border/30">
              <AccordionTrigger className="text-left text-foreground hover:no-underline py-4">
                {faq.question}
              </AccordionTrigger>
              <AccordionContent className="text-muted-foreground pb-4">{faq.answer}</AccordionContent>
            </AccordionItem>
          ))}
        </Accordion>
      </div>
    </section>
  )
}
