"use client"

import { useState } from "react"
import { SECTORS } from "@/lib/types"
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription } from "@/components/ui/dialog"

const sectorDetails: Record<string, { description: string; features: string[] }> = {
  restaurant: {
    description: "Grez vos rservations de tables, menus et promotions facilement.",
    features: ["Gestion des tables", "Menu digital", "Promos automatises", "Confirmation SMS"],
  },
  dentiste: {
    description: "Planifiez les rendez-vous patients et grez vos salles de soins.",
    features: ["Agenda patient", "Rappels automatiques", "Gestion urgences", "Dossiers mdicaux"],
  },
  garage: {
    description: "Suivez les rparations, devis et historique vhicules de vos clients.",
    features: ["Suivi rparations", "Devis automatiques", "Historique vhicules", "Rappels entretien"],
  },
  immobilier: {
    description: "Organisez vos visites et suivez vos prospects efficacement.",
    features: ["Agenda visites", "Suivi prospects", "Matching biens", "Rappels automatiques"],
  },
  juridique: {
    description: "Grez vos consultations et dossiers clients en toute confidentialit.",
    features: ["Agenda consultations", "Gestion dossiers", "Confidentialit", "Facturation"],
  },
  beaute: {
    description: "Planifiez les soins et grez votre clientle beaut.",
    features: ["Rservation soins", "Fichier clients", "Promos cibles", "Rappels RDV"],
  },
  sport: {
    description: "Grez les inscriptions cours et abonnements de vos membres.",
    features: ["Planning cours", "Gestion abonnements", "Rservation crneaux", "Suivi membres"],
  },
  autoecole: {
    description: "Planifiez les leons et suivez la progression de vos lves.",
    features: ["Planning moniteurs", "Suivi progression", "Rservation crneaux", "Examens"],
  },
  veterinaire: {
    description: "Grez les consultations et le suivi sant des animaux.",
    features: ["Agenda consultations", "Dossiers sant", "Rappels vaccins", "Urgences"],
  },
  clinique: {
    description: "Planifiez les consultations mdicales et grez les patients.",
    features: ["Multi-praticiens", "Dossiers patients", "Urgences", "Laboratoire"],
  },
}

export function Sectors() {
  const [selectedSector, setSelectedSector] = useState<string | null>(null)
  const sector = selectedSector ? SECTORS.find((s) => s.value === selectedSector) : null
  const details = selectedSector ? sectorDetails[selectedSector] : null

  return (
    <section id="sectors" className="py-24">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="text-center mb-16">
          <h2 className="text-3xl sm:text-4xl font-bold text-foreground">Adapte  Tous les Secteurs</h2>
          <p className="mt-4 text-lg text-muted-foreground max-w-2xl mx-auto">
            Une solution personnalise pour chaque type d&apos;activit
          </p>
        </div>

        <div className="flex flex-wrap justify-center gap-3">
          {SECTORS.map((sector) => (
            <button
              key={sector.value}
              onClick={() => setSelectedSector(sector.value)}
              className="px-6 py-3 rounded-full glass-card hover:bg-primary/10 hover:border-primary/30 transition-all duration-300 flex items-center gap-2"
            >
              <span className="text-xl">{sector.icon}</span>
              <span className="text-sm font-medium text-foreground">{sector.label}</span>
            </button>
          ))}
        </div>

        <Dialog open={!!selectedSector} onOpenChange={() => setSelectedSector(null)}>
          <DialogContent className="glass-card border-border/50">
            <DialogHeader>
              <DialogTitle className="flex items-center gap-3 text-xl">
                <span className="text-3xl">{sector?.icon}</span>
                {sector?.label}
              </DialogTitle>
              <DialogDescription className="text-muted-foreground">{details?.description}</DialogDescription>
            </DialogHeader>
            <div className="mt-4">
              <h4 className="text-sm font-semibold text-foreground mb-3">Fonctionnalits incluses :</h4>
              <ul className="space-y-2">
                {details?.features.map((feature, index) => (
                  <li key={index} className="flex items-center gap-2 text-sm text-muted-foreground">
                    <span className="w-1.5 h-1.5 rounded-full bg-primary" />
                    {feature}
                  </li>
                ))}
              </ul>
            </div>
          </DialogContent>
        </Dialog>
      </div>
    </section>
  )
}
